.ctaCard {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__text {
		font-size: $font-size-small;
		margin-bottom: 1rem;
		max-width: 45rem;

		@media only screen and (min-width: 1024px) {
			font-size: 1.8rem;
			line-height: 2.5rem;
			margin-bottom: 2rem;
		}
	}
}
